.carderContainer {
    place-items: center;
    margin-top: 30px;
    margin-left: 210px;
    margin-right: 0px;
    margin-bottom: 20px;
    border-radius: 10px;
    padding: 10px;
    width: 50%;
    background-color: #f5f5f5;
}
.banana {
    place-items: center;
    border-radius: 10px;
    margin-top: 20px;
    margin-bottom: 15px;
    width: 50%;
    height: 50%;
    background-color: #f5f5f5;
    font-size :20px;
    color:rgb(0, 0, 0);
    display: block;
}
.Text{
    text-align: center;
    font-family: cursive;
    font-size :60px;
    color:rgb(0, 0, 0);
}

.review {
    width: 100%;
    height: 100vh;
    display: flex;
}
.review .leftSide {
    height: 100%;
    width: 100%;
    flex: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

@media only screen and (max-width: 390px) {
    .carderContainer{
        width: 50%;
        padding: auto;
        align-items: center
    }
    .banana{
        width: 50%;
        padding: auto;
        align-items: center;
    }
}
@media only screen and (max-width: 1300px) {
    .carderContainer{
        margin-left: auto;
        width: 100%;
        padding: auto;
        align-items: center;
    }
    .banana{
        width: 100%;
        padding: auto;
        align-items: center;
    }
    .review {
        width: 100%;
        height: 100vh;
        display: flex;
    }
    .review .leftSide {
        height: 100%;
        width: 100%;
        flex: 0%;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
}