.Container {
    max-width: 960px;
    margin: 0 auto;
}

.header {
    background: rgb(0, 0, 0);
    height: 100px;
}

.header-con {
    display:  flex;
    justify-content: space-between;
    height: 100px;
    align-items: center;
}

.logo-container img {
    width: 200px;
    height: 100px;

}

.menu {
    display: flex;
    list-style-type: none;
}

.menu li {
    padding-right: 2rem;
}

.menu li a {
    text-decoration: none;
    color: #fff;
}

.mobile-menu {
    display:  none;
}

@media only screen and (max-width: 480px) {
    .mobile-menu {
        display:  block;
        color: #fff;
        font-size: 2rem ;
    } 

    .header {
        padding: 0 2rem;
    }
    .menu {
        width: 100%;
        height: auto;
        display: flex;
        position: absolute;
        flex-direction: column;
        top: 83px;
        left: -100%;
        opacity: 0;
        transition:  0.5s ease;
    }
    .menu.active {
        background: #111;
        left: 0;
        opacity: 1;
        z-index: 1;
        padding-left: 0;
        align-content: center;
        transition: 0.5s ease;
    }
    .menu li {
        padding-right: 0;
    }
    .menu-link {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 10vw;
        padding: 10px 0;
    }
   
} 

