.payment {
    width: 100%;
    height: 100vh;
    display: flex;
}

.payment .leftSide {
    height: 100%;
    width: 100%;
    flex: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.PM{
    font-family: cursive;
    font-size :50px;
}

@media only screen and (max-width: 390px){
    .Container .fixed{
        place-items: center;
    }
    .payment {
        width: 100%;
        height: 100vh;
        display: flex;
    }
    
    .payment .leftSide {
        height: 100%;
        width: 100%;
        flex: 0%;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
    
}

@media only screen and (max-width: 1300px){
    .PM{
        font-family: cursive;
        font-size :50px;
    }
}
