.package {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #F5F5F5;
}
.package .packageTitle {
    font-family: cursive;
    font-size: 60px;
}
.packageList {
    width: 70vw;
    height: 80vh;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    place-items: center;
}

/*MENU ITEM STYLING */

.packageItem {
    border-radius: 15px;
    width: 300px;
    height: 350px;
    margin: 20px;
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
}
.package:hover {
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
    transition: 0.3s ease-in;
    cursor: pointer;

}

.packageItem div {
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    width: 100%;
    height: 160px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.packageItem h1 {
    margin-left: 20px;
    font-size: 18px;

}

.packageItem p {
    margin-left: 20px;
    font-size: 15px;
}

.packageItem .BooBoo {
    size: 50;
}

.video-js {
    display: block;
    margin-left:  auto;
    margin-right: auto;
    margin-bottom: 20px;
    border-radius: 10px;
    width: 1080px;
    height:720px;
}

.vjs-poster {
     border-radius: 10px;
}

.video-js .vjs-big-play-button{
    background-color: #ff1100 !important;
}

.card-text-detail{
    width: 1300px;
}

@media only screen and (max-width: 1300px) {
    .packageList {
        grid-template-columns: 1fr 1fr;
    }
    .picture{
        align-items: center;
        place-items: center;
        justify-items: center;
        justify-content: center;
        border-radius: 15px;
        width: 300px;
    }
}

@media only screen and (max-width: 390px) {
    .packageList {
        grid-template-columns: 1fr;
    }
    .card-text-detail{
        width: 330px;
    }
    .picture{
        align-items: center;
        place-items: center;
        justify-items: center;
        justify-content: center;
        border-radius: 15px;
        width: 300px;
    }
    .video-js {
        display: block;
        margin-left:  auto;
        margin-right: auto;
        margin-bottom: 20px;
        border-radius: 10px;
        width:360px;
        height:240px;
    }
}
